<template>
  <div class='bar' @click='click'>
    <div v-if='!isactive'><slot name='img'></slot></div>
    <div v-else><slot name='img-alive'></slot></div>
    <div :style='style'><slot name='text'></slot></div>
 </div>
</template>

<script>
export default {
  name: 'navbottomitem',
    props:{
    path:String,    
    activeColor:{
      type:String,
      default:'red'
    }
  },
  data() { 
    return {

    }
  },
  components:{
  },
  computed:{
    isactive(){
    return this.$route.path===this.path
  },
    style(){
    return this.isactive?{color:this.activeColor}:{}
  }
  },
  methods:{
    click(){
      this.$router.push(this.path).catch(() => { })
      // console.log(this.$route.path===this.path);
      // console.log(this.$route.path);
      // console.log(this.path);
    },

  },
 }
</script>

<style scoped>
.bar{
  flex:1;
  text-align: center;
  height:49px;
}

.bar img{
  width:24px;
  height:24px;
  margin-top:3px;
  vertical-align: middle;
}
</style>